<template>
  <div>
    <Row :gutter="16" style="padding-bottom: 16px">
      <Col span="6">
        <Input v-model="inputText" @input="emptysDearchCommodity">
          <Select v-model="searchType" slot="prepend" style="width: 80px">
            <Option value="name">商品名称</Option>
            <Option value="bar_code">条形码</Option>
          </Select>
          <Button slot="append" icon="ios-search" @click="findCommodity"></Button>
        </Input>
      </Col>
      <Col span="2">
        <Button
          type="warning"
          @click="addCommodityModal"
          :disabled="user.authority.substr(3,4) == 0"
        >新增</Button>
      </Col>
    </Row>
    <Table border :columns="titleList" :data="commodityList">
      <template slot-scope="{ row, index }" slot="action">
        <Button
          type="primary"
          size="small"
          style="margin-right: 5px"
          @click="editCommodityModal(row, index)"
          :disabled="user.authority.substr(1,2) == 0"
        >修改</Button>
        <Button
          type="error"
          size="small"
          @click="deleteCommodityModal(row, index)"
          :disabled="user.authority.substr(2,3) == 0"
        >删除</Button>
      </template>
    </Table>
    <Button @click="loadMore" style="width: 100%" v-if="isShowMoreBtn">加载更多</Button>
    <Modal
      :title="titleModalText"
      :closable="false"
      v-model="addEditCommodityModal"
      @on-ok="addEditCommodityModalOk"
      @on-cancel="addEditCommodityModalCancel"
      class-name="vertical-center-modal"
    >
      <div>
        商品名称：
        <Input type="text" v-model="commodity.name" style="width: 86%" />
      </div>
      <div style="padding-bottom: 10px; padding-top: 10px">
        商品型号：
        <Input type="number" v-model="commodity.weight" style="width: 62%" />
        <span style=" margin-left:10px">（g，kg，mL，L）</span>
      </div>
      <div>
        商品规格：
        <Input type="number" v-model="commodity.specifications" style="width: 66%" />
        <span style=" margin-left:10px">（一件多少个）</span>
      </div>
      <div style="padding-bottom: 10px; padding-top: 10px">
        出产地：&ensp;&ensp;
        <Input type="text" v-model="commodity.home" style="width: 86%" />
      </div>
      <div>
        条形码：&ensp;&ensp;
        <Input type="text" :disabled="isDisabled" v-model="commodity.bar_code" style="width: 86%" />
      </div>
      <div style="padding-bottom: 10px; padding-top: 10px">
        保质期：&ensp;&ensp;
        <Input type="number" v-model="commodity.expire" style="width: 66%" />
        <span style=" margin-left:10px">（单位：天）</span>
      </div>
      <div>
        进货价格：
        <Input type="number" v-model="commodity.price_in" style="width: 66%" />
        <span style=" margin-left:10px">（一个多少钱）</span>
      </div>
      <div style="padding-bottom: 10px; padding-top: 10px">
        正发价格：
        <Input type="number" v-model="commodity.price_out" style="width: 66%" />
        <span style=" margin-left:10px">（一个多少钱）</span>
      </div>
      <div>
        批发价格：
        <Input type="number" v-model="commodity.price_out_down" style="width: 66%" />
        <span style=" margin-left:10px">（一个多少钱）</span>
      </div>
      <div style="padding-top: 10px">
        库存：&ensp;&ensp;&ensp;&ensp;
        <Input type="number" v-model="commodity.stock" style="width: 66%" />
        <span style=" margin-left:10px">（还有多少个）</span>
      </div>
    </Modal>
  </div>
</template>

<script>
import { Http_Post } from "../common/Http";
import {
  findCommodity,
  addCommodity,
  updateCommodity,
  deleteCommodity
} from "../params/CommodityP";

import { keepTwoDecimalFull } from "../common/Util";

export default {
  name: "Commodity",
  data() {
    return {
      user: {}, // 登录用户
      isDisabled: false, // 条形码输入框是否可以使用
      titleModalText: "", // Modal标题文字
      addEditCommodityModal: false, // 是否显示Modal
      titleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "商品图片",
          key: "img_url"
        },
        {
          title: "商品名称",
          key: "name"
        },
        {
          title: "商品型号",
          key: "weight"
        },
        {
          title: "商品规格",
          key: "specifications"
        },
        {
          title: "出产地",
          key: "home"
        },
        {
          title: "条形码",
          key: "bar_code"
        },
        {
          title: "保质期",
          key: "expire"
        },
        {
          title: "进货价格",
          key: "price_in"
        },
        {
          title: "正发价格",
          key: "price_out"
        },
        {
          title: "批发价格",
          key: "price_out_down"
        },
        {
          title: "库存",
          key: "stock"
        },
        {
          title: "商品操作",
          slot: "action",
          width: 130,
          align: "center"
        }
      ], // 列表头
      commodityList: [], // 列表内容commodity
      inputText: "", // 搜索框中的内容
      searchType: "name", // 搜索类型
      commodity: {}, // 提交的到服务器的商品对象
      addOrEdit: 0, // 0是添加，1是修改
      isShowMoreBtn: true, // 是否显示加载跟多按钮
      offset: 0, // 分页加载页标
      limit: 30 // 分页数据量
    };
  },
  methods: {
    // 加载跟多商品
    loadMore() {
      this.offset++;
      this.findCommodity();
    },
    // 显示添加商品的Modal
    addCommodityModal() {
      this.titleModalText = "新增商品";
      this.addOrEdit = 0;
      this.addEditCommodityModal = true;
      this.isDisabled = false;
    },
    // 显示修改商品的Modal
    editCommodityModal(row, index) {
      this.addOrEdit = 1;
      this.commodity.code = row.code;
      this.commodity.name = row.name;
      this.commodity.weight = row.weight;
      this.commodity.specifications = row.specifications;
      this.commodity.home = row.home;
      this.commodity.bar_code = row.bar_code;
      this.commodity.expire = row.expire;
      this.commodity.price_in = row.price_in;
      this.commodity.price_out = row.price_out;
      this.commodity.price_out_down = row.price_out_down;
      this.commodity.stock = row.stock;
      this.commodity.create_time = row.create_time;
      this.addEditCommodityModal = true;
      this.isDisabled = true;
    },
    // 显示删除商品的Modal
    deleteCommodityModal(row, index) {
      let _this = this;
      this.$Modal.confirm({
        title: "警告",
        content: "确定删除此商品？",
        onOk: () => {
          this.$Spin.show();
          Http_Post(
            deleteCommodity(row),
            function(datas) {
              if (datas.msg === 0) {
                _this.$Spin.hide();
                _this.$Message.success("删除成功");
                _this.offset = 0;
                _this.findCommodity();
              } else {
                _this.$Spin.hide();
                _this.$Message.error(datas.error);
              }
            },
            function(error) {
              _this.$Spin.hide();
              _this.$Message.error(error);
            }
          );
        },
        onCancel: () => {}
      });
    },
    // 搜索框为空，查询商品
    emptysDearchCommodity() {
      if (this.inputText === "") {
        this.offset = 0;
        this.findCommodity();
      }
    },
    // 新增或修改商品取消
    addEditCommodityModalCancel() {
      if (this.addOrEdit === 1) {
        this.newCommodity();
      }
    },
    // 新增或修改商品
    addEditCommodityModalOk() {
      let _this = this;
      if (this.commodity.name.length === 0) {
        this.$Message.error("商店名称为空");
        return;
      }
      if (this.commodity.weight.length === 0) {
        this.$Message.error("商品型号：为空");
        return;
      }
      if (
        this.commodity.specifications.length === 0 ||
        this.commodity.specifications == 0
      ) {
        this.$Message.error("商品规格为空");
        return;
      }
      if (this.commodity.home.length === 0) {
        this.$Message.error("出产地为空");
        return;
      }
      if (this.commodity.bar_code.length === 0) {
        this.$Message.error("条形码为空");
        return;
      }
      if (this.commodity.expire.length === 0 || this.commodity.expire == 0) {
        this.$Message.error("保质期为空");
        return;
      }
      // 格式化数据
      this.commodity.price_in = keepTwoDecimalFull(this.commodity.price_in);
      if (this.commodity.price_in === "0.00") {
        this.$Message.error("进货价格为空");
        return;
      }
      // 格式化数据
      this.commodity.price_out = keepTwoDecimalFull(this.commodity.price_out);
      if (this.commodity.price_out === "0.00") {
        this.$Message.error("正发价格为空");
        return;
      }
      // 格式化数据
      this.commodity.price_out_down = keepTwoDecimalFull(
        this.commodity.price_out_down
      );
      if (this.commodity.price_out_down === "0.00") {
        this.$Message.error("批发价格为空");
        return;
      }
      // 库存赋值为0
      if (this.commodity.stock.length === 0) {
        this.commodity.stock = 0;
      }
      if (this.commodity.stock < 0) {
        this.$Message.error("库存库存小于0");
        return;
      }
      this.$Spin.show();
      if (this.addOrEdit === 0) {
        // 新增
        Http_Post(
          addCommodity(_this.commodity),
          function(datas) {
            if (datas.msg === 0) {
              _this.$Message.success("添加成功");
              _this.newCommodity();
              _this.$Spin.hide();
              _this.offset = 0;
              _this.findCommodity();
            } else {
              _this.$Spin.hide();
              _this.$Message.error(datas.error);
            }
          },
          function(error) {
            _this.$Spin.hide();
            _this.$Message.error(error);
          }
        );
      } else {
        // 修改
        Http_Post(
          updateCommodity(_this.commodity),
          function(datas) {
            if (datas.msg === 0) {
              _this.$Message.success("修改成功");
              _this.$Spin.hide();
              _this.offset = 0;
              _this.findCommodity();
            } else {
              _this.$Spin.hide();
              _this.$Message.error(datas.error);
            }
            _this.newCommodity();
          },
          function(error) {
            _this.$Spin.hide();
            _this.$Message.error(error);
            _this.newCommodity();
          }
        );
      }
    },
    // 查询商品
    findCommodity() {
      let _this = this;
      this.$Spin.show();
      Http_Post(
        findCommodity(
          _this.inputText,
          _this.searchType,
          _this.offset,
          _this.limit
        ),
        function(datas) {
          if (datas.msg === 0) {
            if (datas.data.length < _this.limit) {
              _this.isShowMoreBtn = false;
            } else {
              _this.isShowMoreBtn = true;
            }
            if (_this.offset == 0) {
              _this.commodityList = datas.data;
            } else {
              for (var i = 0; i < datas.data.length; i++) {
                _this.commodityList.push(datas.data[i]);
              }
            }
            _this.$Spin.hide();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },
    // 新建空Commodity对象
    newCommodity() {
      this.commodity = {
        code: "",
        name: "",
        weight: 0,
        specifications: 0,
        home: "",
        bar_code: "",
        expire: 0,
        price_in: "0.00",
        price_out: "0.00",
        price_out_down: "0.00",
        stock: 0,
        img_url: "",
        create_time: 0
      };
    }
  },

  created() {
    // 判断是否登录
    if (sessionStorage.getItem("user") == null) {
      this.$router.push({ name: "login" });
      return;
    }
    this.user = JSON.parse(sessionStorage.getItem("user"));
    this.newCommodity();
    this.findCommodity();
  }
};
</script>

<style scoped>
</style>
